.page-header {
  padding: 30px 0 35px;
  background: linear-gradient(180deg, $color-black-pearl 84.9%, rgba(4, 24, 39, 0) 100%);;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: $color-alice-blue;
  z-index: 10;

  @include vp-1023 {
    padding: 20px 0 25px;
  }

  @include vp-767 {
    padding: 10px 0 20px;
  }
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  align-items: start;
}

.page-header__logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 215px;
  margin-right: 9px;

  @include vp-767 {
    width: 35px;
    height: 35px;
    margin: 5px 50px 0 0;
  }
}

.page-header__logo--title {
  @include vp-767 {
    display: none;
  }
}

.page-header__list {
  display: flex;
  flex-wrap: wrap;
  max-width: 510px;
}

.page-header__item {
  margin: 15px 8px 10px 22px;
  width: 225px;

  @include vp-1023 {
    margin: 0 20px 10px 0;
    width: auto;
  }

  @include vp-767 {
    margin: 0 10px 10px;
  }
}

.page-header__link {
  display: block;
  padding: 1px 5px 9px 35px;
  background-repeat: no-repeat;
  background-position: 0 0;

  @include vp-1023 {
    width: 40px;
    height: 40px;
    background-position: center;
  }
}

.page-header__link--email {
  background-image: url('../img/background/email.svg');
}

.page-header__link--phone {
  background-image: url('../img/background/phone.svg');
}

.page-header__link span {
  @include vp-1023 {
    display: none;
  }
}

.page-header__button {
  margin: 5px 60px 10px 10px;

  @include vp-1023 {
    display: none;
  }
}
